h1{margin-bottom: -30px;}

a{text-decoration: none;}

.divin {
    
    width: 300px;
    height: 400px;
    border-radius: 25px;
    display: flex;
    justify-content: start; 
    align-items: baseline;  
    flex-direction: column-reverse;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
}

.divde {
 
    width: 300px;
    height: 400px;
    border-radius: 25px;
    display: flex;
    justify-content: start; 
    align-items: baseline;  
    flex-direction: column-reverse;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    overflow: hidden;           
    transition: transform 0.3s ease;
}

.divtroy {
    
    width: 300px;
    height: 400px;
    border-radius: 25px;
    display: flex;
    justify-content: start; 
    align-items: baseline;  
    flex-direction: column-reverse; 
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    overflow: hidden;            
    transition: transform 0.3s ease;
}

.divin:hover, .divde:hover, .divtroy:hover {
    transform: scale(1.1); 
}

